import React from 'react';
import { Tabs, Tab } from '@material-ui/core';
import { InfoCard } from '@backstage/core-components';






export const ComponentTabs = (props) => {
  const { title, tabs } = props;

  const [value, setValue] = React.useState(0);

  const handleChange = (_event, newValue) => {
    setValue(newValue);
  };

  return (
    React.createElement(InfoCard, { title: title,}
      , React.createElement(Tabs, { value: value, onChange: handleChange,}
        , tabs.map(t => (
          React.createElement(Tab, { key: t.label, label: t.label,} )
        ))
      )
      , tabs.map(({ Component }, idx) => (
        React.createElement('div', {
          key: idx,
          ...(idx !== value ? { style: { display: 'none' } } : {}),}
        
          , React.createElement(Component, null )
        )
      ))
    )
  );
};
